/* modal
----------------------------------------- */
.c-modal__wrap:not(:last-child) {
  margin: 0 0 3.2rem 0;
}

.c-modal__openInner {
  padding: 6rem;
  background-color: #e8fdf5;
}
.c-modal__openInner.pink {
  background-color: #fef0f0;
}

.c-modal__open {
  display: block;
  width: 15rem;
  margin: 0 auto;
  padding: 1.6rem 1.2rem;
  border-radius: 0.4rem;
  background-color: #137752;
  color: #fff;
  transition: transform 0.25s ease;
  font-size: 1.3rem;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.05em;
}
.c-modal__open.pink {
  background-color: #f26e6e;
}
.c-modal__open:hover {
  transform: scale(1.05);
}

.c-modal__close {
  width: 7rem;
  padding: 1rem 1.2rem;
  border-radius: 0.4rem;
  background-color: #bfdcff;
  transition: opacity 0.25s ease;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.1428571429;
  letter-spacing: 0.05em;
}
.c-modal__close:hover {
  opacity: 0.7;
}

.c-modal__closeIcon {
  position: relative;
  width: 2.1rem;
  height: 2.1rem;
}
.c-modal__closeIcon::before {
  position: absolute;
  top: 50%;
  left: 0;
  width: 2rem;
  height: 1px;
  background-color: #000;
  content: "";
  transform: rotateZ(45deg) translate(0%, -50%);
}
.c-modal__closeIcon::after {
  position: absolute;
  top: 50%;
  right: 0;
  width: 2rem;
  height: 1px;
  background-color: #000;
  content: "";
  transform: rotateZ(-45deg) translate(0%, -50%);
}

.c-modal__overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.6);
}

.c-modal {
  display: none;
}
.c-modal.is-open {
  display: block;
}
.c-modal .c-modal__overlay,
.c-modal .c-modal__container {
  will-change: transform;
}
.c-modal[aria-hidden=false] .c-modal__overlay {
  animation: overlayFadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}
.c-modal[aria-hidden=false] .c-modal__container {
  animation: containerFadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}
.c-modal[aria-hidden=true] .c-modal__overlay {
  animation: overlayFadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}
.c-modal[aria-hidden=true] .c-modal__container {
  animation: containerFadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

@keyframes overlayFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes overlayFadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes containerFadeIn {
  0% {
    transform: translateY(15%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes containerFadeOut {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10%);
  }
}
