    font-size: 1.6rem;
    font-weight: 400;
    line-height: 2.75;
    letter-spacing: 0.15em;
  }
}
.c-link.-white {
  color: #fff;
}
.c-link.-white .circle {
  border: 1px solid #fff;
}
.c-link.-white .arrow {
  background-image: url(../images/common/link_arrow_white.svg);
}

.circle {
  display: block;
  border: 1px solid #88b999;
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
  margin: 0 0 0 1.6rem;
  position: relative;
  transition: transform 0.5s cubic-bezier(0.33, 1, 0.68, 1);
}

.arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0.7rem;
  height: 1.1rem;
  background-image: url(../images/common/link_arrow_green.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: transform 0.5s cubic-bezier(0.33, 1, 0.68, 1), opacity 0.5s cubic-bezier(0.33, 1, 0.68, 1);
  transform: translate(-50%, -50%);
}
.arrow:nth-child(2) {
  opacity: 0;
  transform: translate(-150%, -50%);
}

.c-link:hover .circle {
  transform: scale(1.2);
}
.c-link:hover .circle .arrow:nth-child(1) {
  opacity: 0;
  transform: translate(150%, -50%);
}
.c-link:hover .circle .arrow:nth-child(2) {
  opacity: 1;
  transform: translate(-50%, -50%);
}

